import { Component, OnInit, Output } from '@angular/core';
// import { DataService } from '../data.service';
import { CartCounterService } from '../cart-counter.service';
import { DataService } from '../data.service';
@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements OnInit {

 cartCounter: any;


  constructor(private cart: CartCounterService, private data: DataService) { }

  ngOnInit() {
    // this.cartCounter = localStorage.getItem('data').length;
    // this.data.currentValue.subscribe(value => this.cartCounter = value)
    // this.data.getValue().subscribe(
    //   data => {
    //   this.cartCounter = data;
    // }
    // );

    this.data.getCartData().subscribe(
      data =>{
        var a = Object.values(data).length;
        this.cart.changeMessage(a);
      }
    );
    this.cart.currentMessage.subscribe(message => this.cartCounter = message);

  }

}
